import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import './Project.scss';
import projects from '../../assets/projects.json';

const Project = (props) => {
    useEffect(() => {
        const page = document.getElementsByClassName('page')[0];
        if(page){
            page.classList.add('slideInReverse');
            setTimeout(function () {
                page.classList.remove('slideInReverse');
            }, 500)
        }

        return () => {
            if(page){
                page.classList.remove('slideOutReverse');
                setTimeout(function () {
                    page.classList.add('slideOutReverse');
                }, 500)
            }
        }
    }, [])

    return(
        <main>
        <div className="projects">
            <Link to='/' className='back blue'><span>{"<"}</span> Back</Link>
            <div className='left'>
            <div className='text'>
                <div className="sub">{projects[props.page].tech.map((elem, index) => (elem + (index < projects[props.page].tech.length - 1 ? ", " : " ")))}</div>
                <h1>{projects[props.page].title}</h1>
                <p>{projects[props.page].description}</p>
            </div>
            </div>
            <div className='right'>
                <section className="project">
                    <div dangerouslySetInnerHTML={{__html: projects[props.page].text}}/>
                    {projects[props.page].website && <a className='blue' href={projects[props.page].website}>Website</a>}
                </section>
            </div>
        </div>
        </main>
)};
 
export default Project;